import { makePhoneCall,gxgConfirm } from '@/config/package.js'
export default {
	methods: {
		connectWx: function(){
			gxgConfirm({
				title: 'gxg微信号',
				content: 'gxgbasic001',
				showCancel: false,
				confirmText: '确认复制',
				successFunction:()=>{
					uni.setClipboardData({
					   data: 'gxgbasic001',
					    success: function () {
					        uni.showToast({
								title: '已复制到剪切板',
								icon: 'none'
							})
					    }
					});
				}
			})
		},
		makePhone: function(phone){
			makePhoneCall({phone})
		}
	}
}
